import Vue from 'vue'
import Router from 'vue-router'

const  YouXue = r=> require.ensure([],()=>r(require('@/components/youxuezhilv/YouXueZhiLv.vue')))
const  Rank = r=> require.ensure([],()=>r(require('@/components/youxuezhilv/RankPage.vue')))
Vue.use(Router)
export default new Router({
  routes: [
    {
      path: '/',
      name: 'YouXueZhiLv',
      component: YouXue
    },
    {
      path:'/rank',
      name:'RankPage',
      component:Rank
    }
  ]
})



