export class IsPcOrNot {
    constructor() {
    }
    static get = () => {
        let ua = window.navigator.userAgent.toLocaleLowerCase();
        if(ua.indexOf('iphone') != -1|| ua.indexOf('ipad') != -1|| ua.indexOf('ipod') != -1 || ua.indexOf('android') != -1) {
            return false;
        }
        else {
            return true;
        }
    }
}
export default IsPcOrNot;



